.greetingForm {
    color: #000;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
    
}

.error {
    color: red;
}

.input {
   min-width: 372px;
    height: 36px;
    border: 1px solid rgba(209, 209, 209, 1);
    padding: 8px 12px ;
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.input:focus {
 
}

.errorInput {
    min-width: 372px;
    height: 36px;
    border: 1px solid red;
}

.button {
    padding: 5px 24px;
    background-color: #06C;
    opacity: 1;
    border-radius: 3px;
    color: white;
}

.button:focus {
    border: 1px solid blue;
}

.button:active {

}

.button:disabled {
    padding: 5px 24px;
    opacity: 0.5;
}
.text {
    color: rgba(0, 0, 0, 0.50); 
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.greeting {
  color: #000;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
